import React, {Component} from 'react';
import BigNumber from 'bignumber.js';
import numbro from "numbro";
import axios from 'axios';
import {getContracts} from './hooks/contract';

interface StateInterface {
}

class Ethereum extends Component<PropsInterface, StateInterface> {
    
    state = {account:null, project:[]}

    async apyfarm(mchef,pid){

         let apy = 0 
         const furl = 'https://api.degentoken.finance/api/v1/'+mchef+'/farm/'+pid
         const prices = await axios({
            url: furl,
            method: 'get'
            }).then((response) => {
                    apy = new BigNumber(response.data['apy'])
                }); 
           return apy*365
    }

    async apy(mchef,pid){

         let totalapy = 0
         let aprfarm = 0
         let apr = 0
         const furl = 'https://api.degentoken.finance/api/v1/'+mchef+'/'+pid
         const prices = await axios({
            url: furl,
            method: 'get'
            }).then((response) => {
                    apr = new BigNumber(response.data['apr'])
                    aprfarm = new BigNumber(response.data['aprfarm'])
                    totalapy = new BigNumber(response.data['totalapy'])
                }); 
           return totalapy
    }

 
    async refresh_prices(token,currency) {

          let index = token+currency;
          let furl = 'https://api.coingecko.com/api/v3/simple/price?ids='+token+'&vs_currencies='+currency
          await axios({
              url: furl,
              method: 'get'
               }).then((response) => {
                    var data = Object.keys(response.data) ;
                    for (var i = 0; i < data.length; i++){
                        this.setState({ [data[i]+"usd"]: response.data[data[i]]["usd"]});
                        this.setState({ [data[i]+"eth"]: response.data[data[i]]["eth"]});
                        this.setState({ [data[i]+"btc"]: response.data[data[i]]["btc"]});
                        this.setState({ [data[i]+"eur"]: response.data[data[i]]["eur"]});
                        this.setState({ [data[i]+"bnb"]: response.data[data[i]]["bnb"]});
                    }
           });
    }

    async tokenPrice(pair,token,ticker) 
        {
            const pairContract =  await getContracts.PairContract(pair);
            const wethContract = await getContracts.tokenContract("0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c");
            const tokenContract  =  await getContracts.tokenContract(token);


            const tokenAmountWholeLP = await tokenContract.methods
                .balanceOf(pairContract.options.address)
                .call();
            const tokenDecimals = await tokenContract.methods.decimals().call();

            //Number of BNB
            const lpContractWeth = await wethContract.methods
                .balanceOf(pairContract.options.address)
                .call()

            const totalSupply = await pairContract.methods.totalSupply().call() //LP Total SUPPLY => LP
            const lpWethWorth = new BigNumber(lpContractWeth)  //BNB PRICE
            const wethAmount = lpWethWorth.div(new BigNumber(tokenAmountWholeLP))
            return wethAmount
        }



 
    async refresh_lobby(account) {
        
        function isInfinite(num) {
         return !isFinite(num);
        }


        var wethAmount = await this.tokenPrice("0xB4771D6B484d782980Ad00CcBDA58CdcEE34BB22","0xb68a67048596502A8B88f1C10ABFF4fA99dfEc71")
        this.setState({"dgnz-bnb":wethAmount})
        this.setState({"dgnz_usd":wethAmount*this.state.binancecoinusd})

        var wethAmount = await this.tokenPrice("0xBfCf2B3da134308b0F479544784CFC98415dBF85","0xe8B9b396c59A6BC136cF1f05C4D1A68A0F7C2Dd7")
        this.setState({"adgnz-bnb":wethAmount})
        this.setState({"adgnz_usd":wethAmount*this.state.binancecoinusd})

        const DGNZContract = await getContracts.DGNZContract();
        const ADGNZContract = await getContracts.ADGNZContract();

       var poolLen =  await DGNZContract.methods.poolLength().call()

         for (let i=0 ; i< poolLen;i++){


                //console.log(i)
                let project = ""
                let usdPrice = 0
                let bnbPrice = 0
                let token_price = 0
                let DGNZAmountPending = 0 
                let apyval = 0
                let DGNZInfo = await DGNZContract.methods.userInfo(i,account).call();
                DGNZAmountPending = await DGNZContract.methods.pendingDGNZ(i,account).call();
                if (DGNZAmountPending >1){

                    console.log("ENTER INTO PENDING")
                    let interest = parseInt(DGNZAmountPending)/10**18;
                    let interest_price = interest * this.state["dgnz-bnb"]*this.state.binancecoinusd;
                    let DGNZAmount = parseInt(DGNZInfo[0])/10**18;
                    apyval = await this.apyfarm(DGNZContract.options.address,i)
                    //console.log("APY",apyval)

                    if (i==0)
                    {
                        project = "DGNZ";
                        usdPrice =  DGNZAmount*this.state["dgnz_usd"];
                        bnbPrice =  DGNZAmount*this.state["dgnz-bnb"];
                    }
                    else if (i == 5)
                    {
                        project = "ADGNZ";
                        usdPrice =  DGNZAmount*this.state["adgnz_usd"];
                        bnbPrice =  DGNZAmount*this.state["adgnz-bnb"];
                    }

                    else {
                    
                        let poolinfo = await DGNZContract.methods.poolInfo(i).call();
                        const PairContract = getContracts.PairContract(poolinfo[0]);
                        let reserve = await PairContract.methods.getReserves().call();
                        let supply = await PairContract.methods.totalSupply().call();
                        let token0 = reserve[0]*this.state["dgnz-bnb"];
                        let token1 = reserve[1]*this.state["binancecoinbnb"];
                        let t0p = reserve[0]
                        let t1p = reserve[1]


                        const token0addr = await PairContract.methods.token0().call();
                        const token1addr = await PairContract.methods.token1().call();
                        let token0Contract = await getContracts.tokenContract(token0addr);
                        let token0ContractSym = await token0Contract.methods.symbol().call();
                        let token1Contract = await getContracts.tokenContract(token1addr);
                        let token1ContractSym = await token1Contract.methods.symbol().call();

                        let pct = DGNZAmount*10**18/supply;
                        let pctstr = numbro(pct*100).format({average: true, mantissa:2, optionalMantissa: true, spaceSeparated: true })
                        let t0 = numbro(pct*reserve[0]/10**18).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })
                        let t1 = numbro(pct*reserve[1]/10**18).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })
                        project = token0ContractSym+"|"+token1ContractSym+" pool share:"+pctstr+"% "+t0+token0ContractSym+"/"+t1+token1ContractSym
                        token_price = t0p*2/supply*this.state.binancecoinusd
                        usdPrice = token_price*DGNZAmount
                        bnbPrice = usdPrice/this.state.binancecoinusd

                        if (token0ContractSym == "DGNZ"){
                            token_price = t1p*2/supply*this.state.binancecoinusd
                            usdPrice = token_price*DGNZAmount
                            bnbPrice = usdPrice/this.state.binancecoinusd
                            project = token0ContractSym+"|"+token1ContractSym+" pool share:"+pctstr+"% "+t0+token0ContractSym+"/"+t1+token1ContractSym
                        }
                        if (token1ContractSym == "BUSD"){
                            token_price = t1p*2/supply
                            usdPrice = token_price*DGNZAmount
                            bnbPrice = usdPrice/this.state.binancecoinusd
                        }
                    }
                        this.state.project.push(
                         {
                            project: project,
                            type:"Farming",
                            apy: apyval,
                            stakesShares: 0,
                            stakedAmount: DGNZAmount,
                            token_price: token_price,
                            interest: interest,
                            interest_price: interest_price,
                            usdPrice: usdPrice,
                            bnbPrice: bnbPrice
                        });
                    }
        }


         poolLen =  await ADGNZContract.methods.poolLength().call()

         for (let i=0 ; i< poolLen;i++){
            
                //console.log(i)
                let project = ""
                let usdPrice = 0
                let token_price = 0
                let bnbPrice = 0
                let apyval = 0
                let aDGNZAmountPending = 0
                let DGNZInfo = await ADGNZContract.methods.userInfo(i,account).call();
                aDGNZAmountPending = await ADGNZContract.methods.pendingaDGNZ(i,account).call();
                if (aDGNZAmountPending > 1){
                    apyval = await this.apy(ADGNZContract.options.address,i)
                    console.log("ENTER INTO PENDING",i)
                    let aDGNZAmount = await ADGNZContract.methods.stakedWantTokens(i,account).call();
                    let interest = parseInt(aDGNZAmountPending)/10**18;
                    let interest_price = interest * this.state["adgnz-bnb"]*this.state.binancecoinusd;
                    let DGNZAmount = aDGNZAmount/10**18;

                    if (i === 2) {
                        project = "DGNZ";
                        usdPrice =  DGNZAmount*this.state["dgnz_usd"];
                        bnbPrice =  DGNZAmount*this.state["dgnz-bnb"];
                    }

                   else if (i === 6) {
                        project = "CAKE";
                        usdPrice =  DGNZAmount*this.state["pancakeswap-tokenusd"];
                        bnbPrice =  DGNZAmount*this.state["pancakeswap-tokenbnb"];
                        //console.log(bnbPrice,usdPrice)
                    } 

                   else if (i === 19 || i === 24) {
                        project = "WBNB";
                        usdPrice =  DGNZAmount*this.state.binancecoinusd;
                        bnbPrice =  DGNZAmount*this.state.binancecoinbnb;
                    }

                   else if (i === 20 || i === 25) {
                        project = "BUSD";
                        usdPrice =  DGNZAmount;
                        bnbPrice =  DGNZAmount/this.state.binancecoinusd;
                    }

                   else if (i === 21) {
                        project = "ETH";
                        usdPrice =  DGNZAmount*this.state.ethereumusd;
                        bnbPrice =  DGNZAmount*this.state.ethereumbnb;
                    }
                   else if (i === 22) {
                        project = "BTC";
                        usdPrice =  DGNZAmount*this.state.bitcoinusd;
                        bnbPrice =  DGNZAmount*this.state.bitcoinbnb;
                    }
                   else if (i === 23) {
                        project = "USDT";
                        usdPrice =  DGNZAmount;
                        bnbPrice =  DGNZAmount/this.state.binancecoinusd;
                    }

                    else {
                    
                        let poolinfo = await ADGNZContract.methods.poolInfo(i).call();
                        const PairContract = getContracts.PairContract(poolinfo[0]);
                        let reserve = await PairContract.methods.getReserves().call();
                        let supply = await PairContract.methods.totalSupply().call();
                        let t0p = reserve[0]
                        let t1p = reserve[1]

                        const token0addr = await PairContract.methods.token0().call();
                        const token1addr = await PairContract.methods.token1().call();
                        let token0Contract = await getContracts.tokenContract(token0addr);
                        let token0ContractSym = await token0Contract.methods.symbol().call();
                        let token1Contract = await getContracts.tokenContract(token1addr);
                        let token1ContractSym = await token1Contract.methods.symbol().call();
                      

                        let pct = DGNZAmount*10**18/supply;
                        let pctstr = numbro(pct*100).format({average: true, mantissa:2, optionalMantissa: true, spaceSeparated: true })
                        let t0 = numbro(pct*reserve[0]/10**18).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })
                        let t1 = numbro(pct*reserve[1]/10**18).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })
                        project = token0ContractSym+"|"+token1ContractSym+" pool share:"+pctstr+"% "+t0+token0ContractSym+"/"+t1+token1ContractSym
                        token_price = t1p*2/supply*this.state.binancecoinusd
                        usdPrice = token_price*DGNZAmount
                        bnbPrice = usdPrice/this.state.binancecoinusd
                        //console.log(PairContract.options.address,token_price)

                        if ( i == 12 || i == 29 || i == 37 || i == 40){
                            project = token0ContractSym+"|"+token1ContractSym+" pool share:"+pctstr+"% "+t0+token0ContractSym+"/"+t1+token1ContractSym
                            token_price = t1p*2/supply
                            usdPrice = token_price*DGNZAmount
                            bnbPrice = usdPrice/this.state.binancecoinusd
                            //console.log(PairContract.options.address,token_price,supply)
                        }

                        else if (token1ContractSym  == "BNB" || token1ContractSym == "WBNB"){
                            //console.log(i,t1,this.state.binancecoinusd)
                            token_price = (t1p*2/supply)*this.state.binancecoinusd
                            usdPrice = token_price*DGNZAmount
                            bnbPrice = usdPrice/this.state.binancecoinusd
                            project = token0ContractSym+"|"+token1ContractSym+" pool share:"+pctstr+"% "+t0+token0ContractSym+"/"+t1+token1ContractSym
                            //console.log(PairContract.options.address,token_price)
                        }

                        else if (token0ContractSym  == "BNB" || token0ContractSym == "WBNB"){
                            //console.log(i,t0,this.state.binancecoinusd)
                            token_price = (t0p*2/supply)*this.state.binancecoinusd
                            usdPrice = token_price*DGNZAmount
                            bnbPrice = usdPrice/this.state.binancecoinusd
                            project = token0ContractSym+"|"+token1ContractSym+" pool share:"+pctstr+"% "+t0+token0ContractSym+"/"+t1+token1ContractSym
                            //console.log(PairContract.options.address,token_price)
                        }

                    }
                    if ( DGNZAmount > 1/10**17){
                        this.state.project.push(
                             {
                                project: project,
                                type:"Compounding",
                                apy: apyval,
                                stakesShares: 0,
                                stakedAmount: DGNZAmount,
                                token_price:token_price,
                                interest: interest,
                                interest_price: interest_price,
                                usdPrice: usdPrice,
                                bnbPrice: bnbPrice
                            });
                        }
                    }
        }


        let totalBNB = 0;
        let totalInterest = 0;

        for (let i=0 ; i< this.state.project.length;i++){
            totalBNB += this.state.project[i].bnbPrice;
        }
        for (let i=0 ; i< this.state.project.length;i++){
            totalInterest += this.state.project[i].interest_price;
        }
 
        let totalUSD = totalBNB * this.state.binancecoinusd
        let totalBTC = totalBNB * this.state.binancecoinbtc
        let totalEUR = totalBNB * this.state.binancecoineur

        this.setState({project:this.state.project, account:account, totalUSD:totalUSD , totalBTC:totalBTC ,totalEUR:totalEUR, totalInterest:totalInterest});
    }

     async componentDidMount(){
        const account = localStorage.getItem('account');
        const ids = "ethereum,bitcoin,binancecoin,binance-usd,pancakeswap-token,auto"
        const currency = "usd,btc,eth,eur,bnb"
        await this.refresh_prices(ids,currency);
        this.refresh_lobby(account);
    }


    render() {
        return (
            <div id="etherum" class="eth">
                <span> 
                    <b>Crypto Prices:</b>
                <li>
                   <strong>BTC</strong>:{ numbro(this.state.bitcoinusd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                <li>
                   <strong>BNB</strong>:{ numbro(this.state.binancecoinusd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                <li>
                   <strong>DGNZ</strong>:{ numbro(this.state.dgnz_usd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                <li>
                   <strong>ADGNZ</strong>:{ numbro(this.state.adgnz_usd).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$
                </li>
                </span>
                 <span> 
                    <b>Portfolio TVL:</b>
                    <li>
                        <strong>USD:</strong> { numbro(this.state.totalUSD).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}
                    </li>
                    <li>
                        <strong>EUR:</strong> { numbro(this.state.totalEUR).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}
                    </li>
                    <li>
                        <strong>BTC:</strong> { numbro(this.state.totalBTC).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}
                    </li>
                </span>
                 <span> 
                    <b>Pending Interest:</b>
                    <li>
                        { numbro(this.state.totalInterest).format({average: true, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$
                    </li>
                </span>


                <table class="table">
                    <thead>
                    <tr>
                        <th>Pool</th>
                        <th>Type</th>
                        <th>APY</th>
                        <th>Stake Amount</th>
                        <th>LP Price</th>
                        <th>
                            <tr>Interest</tr>
                        </th>
                        <th>BNB TVL (w/o interest)</th>
                        <th>USD TVL (w/o interest)</th>
                    </tr>
                    </thead>
                    <tbody>
                    {this.state.project.map(data =>
                        <tr key={data.project}>
                            <td data-label="pool"><strong>{data.project}</strong></td>
                            <td data-label="type"><strong>{data.type}</strong></td>
                            <td  data-label="apy">{ numbro(data.apy).format({output:"percent",mantissa:0, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="stakeAmount">{ numbro(data.stakedAmount).format({average: false, mantissa:4, optionalMantissa: true, spaceSeparated: true })}</td>
                            <td  data-label="lpprice">{ numbro(data.token_price).format({average: false, mantissa:4, optionalMantissa: true, spaceSeparated: true })}$</td>
                            <td data-label="Interest">
                                { numbro(data.interest).format({average: false, mantissa:8, optionalMantissa: true, spaceSeparated: true })} ({ numbro(data.interest_price).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}$) 
                            </td>
                            <td data-label="BNB TVL">{ numbro(data.bnbPrice).format({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })} BNB</td>
                            <td data-label="USD TVL">{ numbro(data.usdPrice).formatCurrency({average: false, mantissa:2, optionalMantissa: true, spaceSeparated: true })}</td>
                        </tr>
                    )}
                    </tbody>
                </table>
            </div>
        );
    }
}

export default Ethereum;
